
import { defineComponent, ref } from "vue";

export default defineComponent({
  name: "Appbar",
  props: {
    onLogin: Function,
    onChange: Function,
  },
  setup(props) {
    const tabIndex = ref(0);
    const tabList = ["首页", "市场", "指标", "新闻", "关于"];

    function onClick(num: number) {
      tabIndex.value = num;
      if(props.onChange) {
        props.onChange(num);
      }
    }
    return {
      tabIndex,
      tabList,
      onClick,
    };
  },
});
